@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@500&display=swap');

.top{
    max-width: 90%;
    /* border: 1px solid honeydew; */
    /* position: relative; */
    top: 0%;
    margin: auto;
    margin-top: 10vh;

}

.top h2{
    margin-top: 60px;
    margin-bottom: 10px;
    font-size: 29px;
    font-weight: bold;
    text-align: center;
    font-family: 'Noto Sans', sans-serif;
}
.top hr{
    display: block;
    padding: 0;
    margin: 0;
    margin: auto;
    margin-bottom: 45px;
    width:18%;
    text-align: center;
    height:5px;
    border-width: 1px;
    background: linear-gradient(to right, #feac5e, #c779d0, #4bc0c8);
    border-radius: 1px;
    border:0;
}

.skillcard{
    max-width: 80%;
    margin: auto;
    margin-top: 100px;
    display: grid;
    grid-template-columns: repeat(7,1fr);
    gap:35px;
}
.singleCard{
    /* border: 1px solid chartreuse; */
    padding: 7px 6px 7px 6px;
    border-radius: 14px;
    background-color: rgb(39 45 57);
    /* box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px; */
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.singleCard>img{
    width: 90%;
    height:100px;
    margin: auto;
}

.singleCard>p{
    text-align: center;
    font-size: 20px;
    /* font-weight: 100; */
    /* color: #fff; */
    margin-top: 10px;
    font-family: 'Noto Sans', sans-serif;
}

@media screen and (max-width:476px){
    .skillcard{
        margin-top: 0px;
        max-width: 100%;
        grid-template-columns: repeat(2,1fr);
        /* border: 1px solid cornsilk; */
    }
    .singleCard>img{
        width: 60%;
        /* height:130px; */
        margin: auto;
    }
    .singleCard>p{
        font-size: 17px;
    }
    .singleCard{
        padding: 5px 4px 5px 4px;
    }
    .top hr{
        width:30%
    }
}

@media screen and (min-width:477px) and (max-width:900px){
    .skillcard{
        margin-top: 0px;
        max-width: 100%;
        grid-template-columns: repeat(5,1fr);
        /* border: 1px solid cornsilk; */
    }
}